import React, { useState, useEffect, useRef, useCallback } from 'react'
import clsx from 'clsx'
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd'

import DevicesStates from './Devices.config'
import i18n from '../../../controller/i18n'
import Log from '../../../controller/Log'
import FUNCTIONS, { icons as FUNCTION_ICONS, componentsOptions } from '../../../functions'
import ucFirst from '../../../helpers/utils'

import Grid from '@material-ui/core/Grid'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'
import Typography from '@material-ui/core/Typography'

import IconButton from '@material-ui/core/IconButton'

import AddIcon from '@material-ui/icons/Add'
import EditIcon from '@material-ui/icons/Edit'
import DeleteIcon from '@material-ui/icons/Delete'

import ComponentConfig from '../DevicesPage/ComponentConfigPage'
import { JsonEditorPopup } from '../DevicesPage/JsonEditor'
import { StateExplorerPopup } from '../DevicesPage/StateExplorer'
import { Screenshot, getComponents } from '../../../modules/StateList/StateList.settings'

import Collapsible, { CollapsibleSummary, CollapsibleDetails } from '../../../components/Collapsible'
import Dialog from '../../../components/Dialog'
import Icon from '../../../components/Icon'
import Inputs from '../../../components/Inputs'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles'
const useStyles = makeStyles(theme => ({
	fullWidth: {
		width: '100%',
	},
	iconContainer: {
		flexDirection: 'column',
		alignItems: 'center'
	},
	alignRight: {
		display: 'flex',
		justifyContent: 'flex-end',
		
		'& > :not(:first-child)': {
			marginLeft: 8,
		}
	},
	ModifyIcon: {
		verticalAlign: 'bottom'
	},
	heading: {
		margin: 'auto auto auto 0'
	},
	ListItem: {
		padding: 0
	},
	drag: {
		cursor: 'grab !important',
		userSelect: 'none',
		
		'&:hover': {
			backgroundColor: theme.palette.action.hover,
		}
	},
	isDragging: {
		backgroundColor: theme.palette.action.selected,
	}
}));


const _merge = require('deepmerge');
const _copy = require('rfdc')();

const DevicesPage = React.memo(props => {
	const classes = useStyles();
	
	const [device,setDevice] = useState(_copy(props.device));
	const [errors,setErrors] = useState({});
	const EDITOR = useRef({});
	
	/**
	 *
	 */
	const _onChange = useCallback((props, val) => {
		
		// error handler
		setErrors(prevErrors => {
			let nextErrors = { ...prevErrors }
			
			// error occured
			if (props.error && props.error !== prevErrors[props.id]) {
				nextErrors[props.id] = props.error;
				return nextErrors;
			}
			
			// no error -> remove old error
			else if (!props.error && prevErrors[props.id]) {
				delete nextErrors[props.id];
				return nextErrors;
			}
			
			return prevErrors;
		});
		
		// set value
		EDITOR.current[props.id] = val;
		
	}, []);
	
	/*
	 * trigger initial validation
	 */
	useEffect(() => {
		if (!props.device.name) {
			setErrors(prevErrors => ({
				...prevErrors,
				'device->name': true
			}));
		}
		
	}, [props.device.name]);
	
	if (device === null) {
		return null;
	}
	
	/**
	 *
	 */
	const _onSave = () => {
		let savedDevice = { ...device }
		savedDevice.options = savedDevice.options || {}
		savedDevice.attributes = savedDevice.attributes || {}
		
		for (const key in EDITOR.current) {
			const val = EDITOR.current[key];
			const [ type, base, characteristic, param ] = key.split('->');
			
			// states
			if (type === 'states') {
				if (typeof savedDevice[type][base] === 'object' && characteristic.toLowerCase().indexOf('config') > -1) {
					savedDevice[type][base][characteristic] = savedDevice[type][base][characteristic] || {}
					savedDevice[type][base][characteristic][param] = val;
				}
				else if (typeof savedDevice[type][base] === 'object') {
					savedDevice[type][base][characteristic] = val;
				}
				else {
					savedDevice[type][base] = {
						'state': savedDevice[type][base],
						[characteristic]: val
					}
				}
			}
			
			// options or attributes
			else if (type === 'options' || type === 'attributes') {
				savedDevice[type][base] = val;
			}
			
			// device
			else {
				savedDevice[base] = val;
			}
		}
		
		// save
		savedDevice.attributes._updated = Date.now();
		props.onSave(savedDevice);
	}
	
	/**
	 *
	 */
	const _deleteDevice = () => props.onDelete && props.onDelete(device.id);
	
	/**
	 *
	 */
	const _addState = newStateKey => {
		
		const stateKey = newStateKey.replace(/ /g, '');
		setDevice(prevDevice => ({
			...prevDevice,
			states: {
				[stateKey]: {},
				...prevDevice.states || {}
			}
		}));
	}
	
	/**
	 *
	 */
	const _renameState = (stateKey, newStateKey) => {
		newStateKey = newStateKey.replace(/ /g, '');
		
		// verify state key does not exist
		if (device.states[newStateKey] !== undefined) {
			return false;
		}
		
		// replace state key in layout
		//props.onReplaceStateKey(device.id, stateKey, newStateKey); // this is wrong here because Device has to be saved first
		EDITOR.current['states->' + newStateKey + '->_renamed'] = stateKey;
		
		// change state key in EDITOR
		let state = {}
		for (let key in EDITOR.current) {
			const [ type, base, characteristic ] = key.split('->');
			
			if (type === 'states' && base === stateKey) {
				state[characteristic] = EDITOR.current[key];
				EDITOR.current[type + '->' + newStateKey + '->' + characteristic] = EDITOR.current[key];
				delete EDITOR.current[key];
			}
		}
		
		// change state key in device
		setDevice(prevDevice => {
			const nextDevice = { ...prevDevice, 'states': {} }
			
			const states = Object.keys(prevDevice.states);
			const position = states.indexOf(stateKey);
			states.splice(position, 1, newStateKey);
			
			for (let sKey of states) {
				nextDevice.states[sKey] = {
					...prevDevice.states[sKey === newStateKey ? stateKey : sKey],
					...state
				}
			}
			
			return nextDevice;
		});
	}
	
	/**
	 *
	 */
	const _addDefaultStates = func => {
		
		if (FUNCTIONS[func] !== undefined) {
			const stateKeys = Object.keys(device.states || {});
			Object.keys(FUNCTIONS[func]).reverse().forEach(stateKey => {
				const state = FUNCTIONS[func][stateKey];
				
				if (state.substitute !== true && stateKeys.indexOf(stateKey) === -1 && stateKey.substr(0,1) !== '_') {
					_addState(stateKey);
				}
			});
		}
	}
	
	/**
	 *
	 */
	const _moveState = res => {
		
		if (res.source && res.destination && res.source.index !== res.destination.index) {
			
			setDevice(prevDevice => {
				let nextDevice = { ...prevDevice, 'states': {} }
				
				let states = Object.keys(prevDevice.states);
				states.splice(res.destination.index, 0, states.splice(res.source.index, 1)[0]);
				states.forEach(stateKey => {
					nextDevice.states[stateKey] = prevDevice.states[stateKey];
				});
				
				return nextDevice;
			});
			
		}
	}
	
	/**
	 *
	 */
	const _deleteState = stateKey => {
		
		//props.onStateDelete(device.id, stateKey); // this is wrong here because Device has to be saved first
		setDevice(prevDevice => {
			let nextDevice = { ...prevDevice }
			delete nextDevice.states[stateKey];
			return nextDevice;
		});
	}
	
	/*
	 *
	 */
	Log._('DevicesPages', 'RENDER DevicesPage', 'development');
	return (
		<Dialog
			id='DeviceConfiguration'
			header={i18n.t('Device Configuration') + ' (' + i18n.t('Device ID') + ': ' + device.id + ')'}
			actions={[
				<Grid key='actions' container>
					<Grid item xs={6}>
						{device.name && <Inputs.Button key='action->delete' variant="text" onClick={() => _deleteDevice()} color="secondary" startIcon={<DeleteIcon />} label="Delete Device" />}
					</Grid>
					<Grid item xs={6} className={classes.alignRight}>
						<Inputs.Button key='action->cancel' className="cancel" variant="text" onClick={() => props.onClose()} color="secondary" label="Cancel" />
						<Inputs.Button key='action->save' className="apply" variant="text" onClick={() => _onSave()} color="primary" disabled={Object.keys(errors).length > 0} label="Apply" />
					</Grid>
				</Grid>
			]}
			
			dense
			maxWidth='lg'
			fullWidth={true}
			
			open={true}
			onClose={() => props.onClose()}
			>
			
			<Inputs.Fieldset label='General'>
				<Inputs.FieldsetItem columns={3}>
					<Inputs.Text
						key="device->name"
						id="device->name"
						width={200}
						autoFocus
						required
						
						label='Device'
						val={device.name}
						onChange={_onChange}
						/>
					
				</Inputs.FieldsetItem>
				<Inputs.FieldsetItem columns={3}>
					<Inputs.Text
						key="device->label"
						id="device->label"
						width={200}
						
						label='Label'
						val={device.label || ''}
						onChange={_onChange}
						/>
					
				</Inputs.FieldsetItem>
				<Inputs.FieldsetItem columns={3}>
					<Inputs.Select
						key="device->function"
						id="device->function"
						width={200}
						
						label='Function'
						val={device.function || 'other'}
						selection={
							Object.keys(FUNCTIONS).map(funcKey => {
								const func = funcKey !== '_defaults' ? funcKey : 'other';
								return {
									[func]: i18n.t(func),
									'icon': FUNCTION_ICONS[funcKey] || null,
									'_key': func,
									'_translated': true,
									'_sort': i18n.t(func)
								}
							})
						}
						sortOptionsByKey
						
						onChange={(props, val) => {
							
							// add default state keys of function (if not added already)
							if (Object.keys(device.states).length === 0) {
								_addDefaultStates(val);
							}
							
							_onChange(props, val);
							setDevice(prevDevice => ({
								...prevDevice,
								'function': val
							}));
						}}
						/>
					
				</Inputs.FieldsetItem>
				<Inputs.FieldsetItem columns={3}>
					<Inputs.Text
						key="device->icon"
						id="device->icon"
						width={200}
						
						label='Icon'
						val={device.icon || ''}
						onChange={_onChange}
						/>
					
				</Inputs.FieldsetItem>
				
				<Inputs.FieldsetItem columns={3}>
					<Inputs.Switch
						key="device->suppressPopup"
						id="device->suppressPopup"
						width={200}
						
						label='Suppress Popup'
						info='Do not open the device details in the popup when the device is clicked'
						
						val={device.suppressPopup !== undefined ? device.suppressPopup : false}
						onChange={_onChange}
						/>
					
				</Inputs.FieldsetItem>
			</Inputs.Fieldset>
			
			<Inputs.Fieldset label='States'>
				<DeviceStates
					_onChange={_onChange}
					_addState={_addState}
					_addDefaultStates={_addDefaultStates}
					_renameState={_renameState}
					_moveState={_moveState}
					_deleteState={_deleteState}
					errors={errors}
					device={device}
					/>
					
			</Inputs.Fieldset>
		</Dialog>
	)
});

export default DevicesPage;


/*
 *
 */
const AddDeviceState = React.memo(props => {
	Log._('DevicesPages', 'RENDER AddDeviceState', 'development');
	const { device } = props;
	
	const StateAddRef = useRef();
	const [error,setError] = useState('');
	
	const onAddState = () => {
		const newStateKey = StateAddRef.current.value || '';
		
		// empty state key
		if (!newStateKey) {
			setError(i18n.t('Type in a state key / identifier'));
		}

		// state key exists already
		else if (Object.keys(device.states || {}).indexOf(newStateKey) > -1) {
			setError(i18n.t('State key already used'));
		}
		
		// add state
		else {
			setError('');
			props._setNewStateKey(newStateKey)
			props._addState(newStateKey);
		}
	}
	
	return (
		<ListItem key='StatesListItemLabel->add' dense divider className="DeviceStateAdd">
			<ListItemIcon>
				<IconButton size="small" disabled>
					<AddIcon />
				</IconButton>
			</ListItemIcon>
			
			<ListItemText disableTypography style={{ display: 'flex' }}>
				<Inputs.Text
					id='states->add'
					ref={StateAddRef}
					shrink={true}
					label='new state key'
					val={props.newStateKey}
					onEnter={onAddState}
					/>
				
				<div style={{ display: 'flex', margin: 'auto 0' }}>
					<Inputs.Button
						className="DeviceStateAddButton"
						label='add state'
						onClick={onAddState}
						width={275}
						/>
					
					<Inputs.Button
						label='add default states'
						onClick={() => props._addDefaultStates(device.function)}
						width={275}
						/>
					
				</div>
				<span style={{ color: '#900' }}>{error}</span>
			</ListItemText>
		</ListItem>
	);
});

/*
 *
 */
const DeviceStates = React.memo(props => {
	Log._('DevicesPages', 'RENDER DeviceStates', 'development');
	const { device, _moveState } = props;
	const classes = useStyles();
	
	const newStateKey = useRef('');
	const setNewStateKey = val => newStateKey.current = val;
	
	const renderedStates = Object.keys(device.states || {}).map((stateKey, i) => {
		let state = device.states[stateKey];
		state = typeof state === 'string' ? { 'state': state } : state;
		
		return (
			<DragDeviceState
				{...props}
				key={'DeviceState->' + stateKey}
				index={i}
				state={{ ...state, 'key': stateKey }}
				expanded={stateKey === newStateKey.current}
				/>
			
		);
	});
	
	return (
		<List classes={{ root: classes.fullWidth }} dense>
			<AddDeviceState newStateKey={newStateKey.current} _setNewStateKey={setNewStateKey} {...props} />
			<DragDropContext onDragEnd={_moveState}>
				<Droppable
					key='DeviceStates'
					droppableId='states'
					>
					
					{(provided, snapshot) => (
					<div
						ref={provided.innerRef}
						{...provided.droppableProps}
						>
						
						{renderedStates}
						{provided.placeholder}
					</div>
					)}
				</Droppable>
			</DragDropContext>
		</List>
	);
});

/*
 *
 */
const DragDeviceState = props => {
	const classes = useStyles();
	
	return (
		<Draggable
			key={props.state.key}
			draggableId={'state->' + props.state.key}
			index={props.index}
			>
			
			{(provided, snapshot) => (
			<div
				className={clsx(classes.drag, snapshot.isDragging && classes.isDragging)}
				ref={provided.innerRef}
				{...provided.draggableProps}
				{...provided.dragHandleProps}
				>
				
				<DeviceState {...props} />
			</div>
			)}
		</Draggable>
	);
}

/*
 *
 */
const DeviceState = React.memo(props => {
	Log._('DevicesPages', 'RENDER DeviceState', 'development');
	const { state, expanded, _renameState, _deleteState } = props;
	const classes = useStyles();
	
	const fields = Object.keys(DevicesStates).map(field => <DeviceStateField key={'DeviceStateField->' + state.key + '->' + field} {...props} field={field} />);
	const renameState = () => {
		const stateKey = prompt(i18n.t('Rename State Key'), state.key);
		
		if (stateKey && stateKey !== state.key) {
			_renameState(state.key, stateKey);
		}
	}
	
	return (
		<ListItem
			key={'StatesListItem->' + state.key}
			classes={{ root: clsx(classes.ListItem, 'DeviceState') }}
			dense
			>
			
			<ListItemText disableTypography>
				<Collapsible key={'DeviceState->' + state.key} expanded={expanded} className={'DeviceState DeviceState->' + state.key}>
					<CollapsibleSummary>
						<Typography classes={{ root: classes.heading }} display="inline">{state.key}</Typography>
						
						<IconButton size="small" onClick={renameState}>
							<EditIcon />
						</IconButton>
						<IconButton size="small" onClick={e => {e.stopPropagation();_deleteState(state.key)}}>
							<DeleteIcon />
						</IconButton>
						<Icon style={{ fontSize: '1.5em', margin: 'auto 8px' }} icon="mdi:drag" />
						
					</CollapsibleSummary>
					<CollapsibleDetails>
						<Inputs.Fieldset noPadding>
							{fields}
						</Inputs.Fieldset>
					</CollapsibleDetails>
				</Collapsible>
			</ListItemText>
		</ListItem>
	);
});

/*
 *
 */
const DeviceStateField = React.memo(props => {
	const { device, field } = props;
	const fieldProps = DevicesStates[field];
	const deviceDefaults = _merge(FUNCTIONS['_defaults'], FUNCTIONS[device.function] || {});
	
	// JSON
	if (fieldProps.json === true) {
		return <DeviceStateFieldJsonEditor {...props} fieldProps={fieldProps} deviceDefaults={deviceDefaults} />
	}
	
	// AutoComplete
	else if (fieldProps.options !== undefined) {
		return <DeviceStateFieldAutoComplete {...props} fieldProps={fieldProps} deviceDefaults={deviceDefaults} />
	}
	
	// State Explorer
	else if (field === 'state' || field === 'action') {
		return <DeviceStateFieldStateExplorer {...props} fieldProps={fieldProps} deviceDefaults={deviceDefaults} />
	}
	
	// Other
	else {
		return <DeviceStateFieldOther {...props} fieldProps={fieldProps} deviceDefaults={deviceDefaults} />
	}
});

const DeviceStateFieldJsonEditor = React.memo(props => {
	const { state, field, _onChange } = props;
	
	
	const value = useRef(state[field] && state[field].node ? state[field].node : (state[field] || ''));
	const setValue = val => value.current = val;
	const [showJsonEditor,setShowJsonEditor] = useState(false);
	const onChange = (props, val) => !props.error && _onChange(props, val);
	
	// additional props
	let additionalProps = {}
	additionalProps.action = () => setShowJsonEditor(true);
	
	return (
		<React.Fragment key={'StatesListItemField->' + state.key + '->' + field}>
			<JsonEditorPopup
				open={showJsonEditor}
				_onClose={() => setShowJsonEditor(false)}
				val={value.current}
				height={500}
				_onChange={val => {
					onChange({ 'id': 'states->' + state.key + '->' + field }, val);
					setValue(val);
				}}
				/>
			
			<DeviceStateFieldInput {...props} additionalProps={additionalProps} val={value.current} setVal={setValue} _onChange={onChange} />
		</React.Fragment>
	);
});

const DeviceStateFieldStateExplorer = React.memo(props => {
	const { state, field, _onChange } = props;
	
	
	const [value,setValue] = useState(state[field] && state[field].node ? state[field].node : (state[field] || ''));
	const setVal = val => setValue(val);
	const [showStateExplorer,setShowStateExplorer] = useState(false);
	
	// additional props
	let additionalProps = {}
	additionalProps.action = () => setShowStateExplorer(true);
	
	return (
		<React.Fragment key={'StatesListItemField->' + state.key + '->' + field}>
			
			<StateExplorerPopup
				open={showStateExplorer}
				_onClose={() => setShowStateExplorer(false)}
				val={value}
				_onChange={val => {
					_onChange({ 'id': 'states->' + state.key + '->' + field }, val);
					setValue(val);
					setShowStateExplorer(false);
				}}
				/>
			
			<DeviceStateFieldInput {...props} additionalProps={additionalProps} val={value} setVal={setVal} />
		</React.Fragment>
	);
});

const DeviceStateFieldAutoComplete = React.memo(props => {
	const { device, deviceDefaults, state, field, fieldProps, _onChange } = props;
	
	
	// components
	const components = {
		'screenshot': (option, state) => <Screenshot func={device.function} option={option} selected={state.selected} />,
		'bodyElement': getComponents(device.function, 'body'),
		'actionElement': getComponents(device.function, 'action')
	}
	
	const [value,setValue] = useState(
		(state[field] && state[field].node !== undefined) || state[field] !== undefined ? // prefer if given but empty (which means user has unselected it)
			((state[field] && state[field].node) || state[field] || '') : (
				(field.indexOf('Element') > -1 && deviceDefaults[state.key.toLowerCase()] && deviceDefaults[state.key.toLowerCase()][field]) ||
				(field.indexOf('Element') > -1 && deviceDefaults['_any'] && deviceDefaults['_any'][field]) ||
				''
			)
	);
	
	const setVal = val => setValue(val);
	const [componentConfig,setComponentConfig] = useState(null);
	
	//
	const property = useRef({});
	useEffect(() => {
		
		if (value && property.current[value] === undefined) {
			let config = _merge(
				componentsOptions['_defaults'][value] || {},
				(componentsOptions[props.device.function] && componentsOptions[props.device.function][value]) || {}
			);
			
			// apply user specific values
			const userConfig = props.state[value + 'Config'] || {}
			Object.keys(userConfig).forEach(c => {
				config[c] = typeof config[c] !== 'object' ? { 'val': config[c] } : config[c];
				config[c].val = userConfig[c];
			});
			
			property.current[value] = config;
		}
		
	}, [value, props.device, props.state]);
	
	// additional props
	let additionalProps = {}
	if (fieldProps.options !== undefined) {
		additionalProps.options = typeof fieldProps.options === 'function' ? fieldProps.options(props.device) : components[field];
	}
	
	if (fieldProps.renderOption !== undefined) {
		additionalProps.renderOption = typeof fieldProps.renderOption === 'function' ? ((option, state) => fieldProps.renderOption(option, state, props.device)) : components['screenshot'];
	}
	
	additionalProps.action = ((componentsOptions['_defaults'] && componentsOptions['_defaults'][value]) || (componentsOptions[device.function] && componentsOptions[device.function][value])) && (() => setComponentConfig({ 'id': field + 'Config', 'config': property.current[value] }));
	
	return (
		<React.Fragment key={'StatesListItemField->' + state.key + '->' + field}>
			
			<ComponentConfig
				_onClose={() => setComponentConfig(null)}
				_onChange={(props, val) => {
					const key = props.id.substr(props.id.lastIndexOf('.')+1);
					property.current[value][key].val = val;
					_onChange({ ...props, 'id': 'states->' + state.key + '->' + value + 'Config->' + key }, val);
				}}
				id={(componentConfig && componentConfig.id) || null}
				config={(componentConfig && componentConfig.config) || {}}
				device={device}
				/>
			
			<DeviceStateFieldInput {...props} additionalProps={additionalProps} val={value} setVal={setVal} />
		</React.Fragment>
	);
});

const DeviceStateFieldOther = React.memo(props => {
	const { state, field } = props;
	
	
	const [value,setValue] = useState(state[field] && state[field].node ? state[field].node : (state[field] || ''));
	const setVal = val => setValue(val);
	
	return (
		<React.Fragment key={'StatesListItemField->' + state.key + '->' + field}>
			<DeviceStateFieldInput {...props} val={value} setVal={setVal} />
		</React.Fragment>
	);
});

const DeviceStateFieldInput = React.memo(props => {
	const { deviceDefaults, state, field, fieldProps, additionalProps, setVal, _onChange } = props;
	
	
	const Input = Inputs[fieldProps.type || 'Text'];
	
	return (
		<Inputs.FieldsetItem className={'field->' + field} columns={4}>
			<Input
				{...fieldProps}
				{...additionalProps}
				
				id={'states->' + state.key + '->' + field}
				shrink={true}
				label={fieldProps.label || ucFirst(field)}
				placeholder={(deviceDefaults[state.key.toLowerCase()] && deviceDefaults[state.key.toLowerCase()][field.replace('Element', '')]) || fieldProps.val || ''}
				width={370}
				val={props.val}
				
				onChange={(props, val) => {
					setVal(val);
					typeof fieldProps.onChange === 'function' ? fieldProps.onChange(props, val, _onChange) : _onChange(props, val);
				}}
				/>
				
		</Inputs.FieldsetItem>
	);
});
